"use client";

import { motion } from "motion/react";
import Image from "next/image";
import Link from "next/link";
import { useState, useRef } from "react";
import { useTransitionRouter } from "next-view-transitions";
import { pageTransition } from "@/constants/pageTransition";
import useInitialLoad from "@/contexts/initial-load-context";
import { Project } from "@/data/projects";

export default function ProjectCard({
  project,
  index,
}: {
  project: Project;
  index: number;
}) {
  const [isHovered, setIsHovered] = useState(false);
  const router = useTransitionRouter();
  const { isInitialLoad } = useInitialLoad();

  return (
    <motion.div
      initial={{ y: 24, opacity: 0 }}
      animate={{ y: 0, opacity: 1 }}
      transition={{
        duration: 1.5,
        delay: (isInitialLoad ? 3 : 1) + index * 0.075,
        ease: [0.16, 1, 0.3, 1],
      }}
    >
      <Link
        href={`/work/${project.slug}`}
        className="flex flex-col gap-3 cursor-pointer group"
        onMouseEnter={() => setIsHovered(true)}
        onMouseLeave={() => setIsHovered(false)}
        onClick={(e) => {
          e.preventDefault();
          router.push(`/work/${project.slug}`, {
            onTransitionReady: pageTransition,
          });
        }}
      >
        {/* Image container */}
        <div className="relative w-full aspect-[4/3] overflow-hidden">
          {project.videoUrl ? (
            <video
              src={project.videoUrl}
              className={`absolute inset-0 w-full h-full object-cover transition-opacity duration-500 ${
                isHovered ? "opacity-100" : "opacity-0"
              }`}
              muted
              loop
              playsInline
              autoPlay={isHovered}
            />
          ) : null}
          
          <Image
            src={project.backgroundImageUrl}
            alt={project.title}
            fill
            className={`object-cover transition-opacity duration-500 ${
              project.videoUrl && isHovered ? "opacity-0" : "opacity-100"
            }`}
          />
        </div>

        {/* Text container */}
        <div className="flex justify-between items-start pt-2">
          <div className="flex gap-2">
            <span className="text-xs font-mono text-neutral-400 tracking-tight">
              {String(index + 1).padStart(2, "0")}
            </span>
            <span className="text-sm font-medium text-neutral-100">
              {project.title}
            </span>
          </div>
          
          <span className="text-xs font-mono text-neutral-400 tracking-tight">
            {project.year}
          </span>
        </div>
      </Link>
    </motion.div>
  );
}